"use client"

import { useState, useEffect } from "react"
import Link from "next/link"
import { motion, AnimatePresence } from "framer-motion"
import { Menu, X, Anchor } from "lucide-react"
import { Button } from "@/components/ui/button"

export function Navbar() {
  const [isScrolled, setIsScrolled] = useState(false)
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50)
    }
    window.addEventListener("scroll", handleScroll)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  const navLinks = [
    { href: "/", label: "HOME" },
    { href: "/yachts", label: "YACHTS" },
    { href: "/destinations", label: "DESTINATIONS" },
    { href: "/booking", label: "BOOK NOW" },
  ]

  return (
    <>
      <motion.nav
        initial={{ y: -100 }}
        animate={{ y: 0 }}
        transition={{ duration: 0.6, ease: "easeOut" }}
        className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 ${
          isScrolled ? "bg-white/95 backdrop-blur-md shadow-sm" : "bg-transparent"
        }`}
      >
        <div className="max-w-7xl mx-auto px-6 lg:px-8">
          <div className="flex items-center justify-between h-20">
            {/* Logo */}
            <Link href="/" className="flex items-center gap-2">
              <Anchor className={`h-8 w-8 ${isScrolled ? "text-black" : "text-white"}`} />
              <span className={`text-xl font-bold tracking-wider ${isScrolled ? "text-black" : "text-white"}`}>
                DUBAI YACHTS
              </span>
            </Link>

            {/* Desktop Navigation */}
            <div className="hidden md:flex items-center gap-8">
              {navLinks.map((link) => (
                <Link
                  key={link.href}
                  href={link.href}
                  className={`text-sm font-medium tracking-widest transition-colors hover:text-[#1E5AFF] ${
                    isScrolled ? "text-black" : "text-white"
                  }`}
                >
                  {link.label}
                </Link>
              ))}
              <Link href="/admin">
                <Button className="bg-[#1E5AFF] hover:bg-[#1E5AFF]/90 text-white rounded-full px-6">ADMIN</Button>
              </Link>
            </div>

            {/* Mobile Menu Button */}
            <button className="md:hidden" onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}>
              {isMobileMenuOpen ? (
                <X className={`h-6 w-6 ${isScrolled ? "text-black" : "text-white"}`} />
              ) : (
                <Menu className={`h-6 w-6 ${isScrolled ? "text-black" : "text-white"}`} />
              )}
            </button>
          </div>
        </div>
      </motion.nav>

      {/* Mobile Menu */}
      <AnimatePresence>
        {isMobileMenuOpen && (
          <motion.div
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            className="fixed inset-0 z-40 bg-white pt-24 px-6 md:hidden"
          >
            <div className="flex flex-col gap-6">
              {navLinks.map((link) => (
                <Link
                  key={link.href}
                  href={link.href}
                  onClick={() => setIsMobileMenuOpen(false)}
                  className="text-2xl font-medium tracking-widest text-black hover:text-[#1E5AFF] transition-colors"
                >
                  {link.label}
                </Link>
              ))}
              <Link href="/admin" onClick={() => setIsMobileMenuOpen(false)}>
                <Button className="bg-[#1E5AFF] hover:bg-[#1E5AFF]/90 text-white rounded-full px-8 py-6 text-lg w-full mt-4">
                  ADMIN PANEL
                </Button>
              </Link>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  )
}
